.main {
  margin: 0 !important;
  padding: 0 !important;
  text-align: center;
  height: 100vh;
  }

.main > img{
  width: 100%;
  height: 100%;
}
  
.main > a,
.main > h2{
  color: rgb(255, 255, 255);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 4rem;
}

.main > a> button{
  transform: translate(0%, 200%);
  padding: 30px;
  width: 80%;
  border-radius: 50px;
  font-size: 1.5rem;
  border: none;
  font-weight: bold;
  background-color: darkorange;
  cursor: pointer;
}

.main > a > button:hover{
  background-color: #0B4F84;
  color: white;
  box-shadow: 0 0px 20px yellow;
}

@media only screen and (max-width: 720px){
  .main > a > button{
    transform: translate(0%, 300%);
    
  }
}