.meal{
    width: 100%;
    height: 100%;
    background-color: #EAF6F6;
    background-image: url("https://www.transparenttextures.com/patterns/absurdity.png");
}

.meal > .meals-body{
    padding: 90px;
    /* position: absolute;
    z-index: 0; */
}

.meal > .meals-body > .text{
    font-size: 1.2rem;
    box-shadow: 0 10px 8px gray;
    padding:20px;
    border-radius: 30px;
}

.meal > .meals-body > .text:hover{
    box-shadow: 10px 10px 10px tomato;
    background-color: gray;
    color: yellow;
}

.meal > .meals-body > .choice {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;

}

.meal > .meals-body > .choice > img{
    border-radius: 45%;
    margin: 40px 0;
}

.meal > .meals-body >.choice > h3 {
    color: #335B92;
    font-size: 2rem;
    width: 80%;
}


.meal > .meals-body > .list-items {
    margin-top: 30px;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, 470px);
    grid-template-rows: repeat(auto-fill, 600px);
    gap: 35px;
    justify-content: center;
}

.meal > .meals-body > .list-items > .item {
    min-width: 360px;
    box-shadow: 0 10px 20px grey;
    padding: 10px;
    border-radius: 5px;
}

.meal > .meals-body > .list-items> .item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}

.meal > .meals-body > .list-items > .item > img {
    border-radius: 5px;
}

.meal > .meals-body > .list-items > .item > ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}

.meal > .meals-body > .list-items > .item > ul > li > span {
    background-color: grey;
    padding: 5px;
    border-radius: 20px;
    color: #fff;
}

.meal > .meals-body > .list-items > .item> h4 {
    margin: 30px;
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    color: grey;
}

.meal > .meals-body > .list-items > .item > a{
    width: 100% !important;
}

.meal > .meals-body > .list-items > .item> a > button {
    margin-top: 15px;
    text-align: center;
    padding: 10px;
    color: white;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    background-color: rgb(81, 108, 136);
    font-size: 20px;

}
.meal > .meals-body > .list-items > .item> a > button:hover {
    width: 300px;
    transform: scale(1.05);
}



@media only screen and (max-width: 700px) {
    
    .meal > .meals-body{
        padding: 0px !important;
        margin:0 5%;
        width: 90%;
    }
    
    .meal > .meals-body > .text{
        padding: 15px;
        margin-top: 90px;
    }
    
    .meal> .meals-body> .choice{
        width: 100%;
        flex-direction: column;
        text-align: center;
    }
    
    .meal> .meals-body> .choice> h3{
        font-size: 1.5rem;
    }
    
    .meal > .meals-body > .list-items{
        margin: 0 !important;
        display: flex;
        flex-direction: column;
        width: 100% !important;
        justify-content: center;
        align-items: center;
    }
    
    .meal > .meals-body > .list-items > .item{
        width: 90% !important;
    }

    .meal > .meals-body > .list-items > .item> img{
        width: 100%;
        text-align: center;
    }
}


@media only screen and (max-width: 915px){

    .meal > .meals-body > .list-items{
        display: flex;
        flex-direction: column;
        width: 80%;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: 0 10%;
    }


}

@media only screen and (max-width: 1200px){
    .meal > .meals-body{
        padding: 30px;
    }

    .meal> .meals-body >.list-items {
        grid-template-columns: repeat(auto-fill, 400px);
    }

    .meal .meals-body > .list-items > .item{
        width: 100%;
    }


    .meal > .meals-body > .list-items > .item > img{
        width: 100%;
    }

}

