.header{
    width: 100%;
    height: 100%;
    margin-bottom: 50px;
}

.container{
    display: flex;
    justify-content: space-around;
    align-content: center;
}

.left{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    margin-left: 30px;
}

.left .title{
    font-weight: bold;
    color: tomato;
    font-size: 2rem;
    font-family: 'Stick', sans-serif;

}

.left > img {
    width: 50%;
}

.right {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    margin-right: 30px;
}

.right > a{
    list-style: none;
    background-color: #72C063;
    cursor: pointer;
    border-radius: 20px;
    margin-right: 20px;
}

.right >button{
    background-color: white;
    list-style: none;
    border: none;
    cursor: pointer;
    border-radius: 100%;
    color: red;
}

.right > button:hover {
    box-shadow: 0 1px 2px tomato;
    

}
.right > a > button{
    font-weight: 500;
    border: none;
    font-size: 1rem;
    background: none;
    margin: 20px;
    cursor: pointer;
}


.right > a > button:focus{
    outline: none;
}

.right > a:hover{
    box-shadow: 0 1px 30px rgb(37, 97, 37);
}


.right > .bars,
.right > .close
{
    display: none;
}

.right > .bars:focus,
.right > .close:focus
{
    outline: none;
}




@media only screen and (max-width: 700px){

    .container1{
        width: 100%;
        flex-direction: column;
        height: 100vh;
        position: relative;
        z-index: 1;
        background-color: gray;
    }

    .container1 > .left {
        display: flex;
        flex-direction: column;
        align-content:center ;
        position: relative;
        top: 100px;
    }

    .container1 > .left > h2{
        margin-bottom: 30px;
        font-size: 3rem;
        color: #8DB93D;
    }

    .container1 > .left > img{
        border-radius: 100%;
    }

   .container1 .right{
        flex-direction: column;
        text-align: center;
        position: relative;
        top: 310px;
        z-index: 2;
    }

    .container1 > .right > a,
    .container1 > .right > .close{
        display: block;
        width: 100%;
        margin-right: 0;
        margin-left: 5%;
    }

    .container1 > .right > a > button{
        color: white;
    }

    .container1 > .right > .bars{
        display: none;
    }

    .container1 > .right > .close{
        position: relative;
        top: -450%;
        right: 30%;
        box-shadow: none;
        width: 0 !important;
        background-color: gray;
        height: 0 !important;
    }

    .right > a{
        width: 80%;
        display: none;
        background: none !important;
    }

    .right > .bars
     {
        display: block;
    }

}