.meals-detail {
    padding-top: 100px;
    text-align: center;
}

.meals-detail>h1 {
    font-size: 5rem;
    color: rgb(81, 98, 64);
    margin-bottom: 50px;
}

.meals-detail>.body-detail>.image {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}


.meals-detail>.body-detail>.cook {
    margin: 60px 0;
    padding: 0 90px;
    display: flex;
    justify-content: space-evenly;
}

.meals-detail>.body-detail>.cook>.ingerdients,
.meals-detail>.body-detail>.cook>.intstruction {
    box-shadow: 3px 5px 10px tomato;
    border-radius: 50px;
    width: 410px;
}

.meals-detail>.body-detail>.cook>.intstruction {
    margin-left: 150px;
}

.meals-detail>.body-detail>.cook>.ingerdients>img,
.meals-detail>.body-detail>.cook>.intstruction>img {
    border-radius: 50px;
    box-shadow: 0px 5px 20px tomato;
}

.meals-detail>.body-detail>.cook>.ingerdients>h2,
.meals-detail>.body-detail>.cook>.intstruction>h2 {
    margin-top: 30px;
    font-size: 2rem;
    color: darkblue;
    text-shadow: 0px 2px 5px tomato;
}

.meals-detail>.body-detail>.cook>.ingerdients>ul,
.meals-detail>.body-detail>.cook>.intstruction>ul {
    padding: 25px;
    margin-top: 15px;
    text-align: left;
    font-size: 1.25rem;
}

.meals-detail>.body-detail>.note {
    margin: 0 25%;
    box-shadow: 3px 5px 10px tomato;
    border-radius: 50px;
    margin-bottom: 60px;
    padding: 25px;
    margin-top: 15px;
    text-align: left;
    font-size: 1.25rem;
}

.meals-detail>.body-detail>.note>ul>h2 {
    text-align: center;
    margin-bottom: 20px;
    margin-top: 0;
    font-size: 2rem;
    color: darkblue;
    text-shadow: 10px 2px 5px tomato;
}

.meals-detail>.body-detail>iframe {
    border-radius: 25px;
}

@media only screen and (max-width : 1020px) {

    .meals-detail>.body-detail>.cook>.ingerdients,
    .meals-detail>.body-detail>.cook>.intstruction {
        width: 50%;
    }

    .meals-detail>.body-detail>.cook>.intstruction {
        margin-left: 60px;
    }

    .meals-detail>.body-detail>.cook>.ingerdients>img,
    .meals-detail>.body-detail>.cook>.intstruction>img {
        width: 100%;
    }



    .meals-detail>.body-detail>iframe {
        width: 80%;
    }
}

@media only screen and (max-width : 800px) {
    .meals-detail>.body-detail>.cook {
        flex-direction: column;
        align-items: center;
    }

    .meals-detail>.body-detail>.cook>.intstruction {
        margin: 60px 0;
    }

    .meals-detail>.body-detail>.cook>.ingerdients,
    .meals-detail>.body-detail>.cook>.intstruction {
        width: 100%;
    }

    .meals-detail > .body-detail > .note{
        width: 50%;
        margin: 50px 20%;
    }

}