.detail-body{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-top: 80px;
    padding: 20px 0;
    box-shadow: 3px 5px 6px tomato;
    background-color: grey;
    color: white;
    box-sizing: content-box;
    height: 400px;
}

.detail-body>.left{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 50%;
    height: 200px;
}

.detail-body > .left > h2{
    font-size: 3rem;
    margin-bottom: 20px;
    color: white;
}

.detail-body > .left > ul{
    display: flex;
    list-style: none;
    justify-content: space-between;
}

.detail-body > .left > ul > li{
    margin: 10px;
}

.detail-body > .right > img {
    border-radius: 30px;
}


.detail > .exercise {
    padding: 90px 0 0 90px;
}

.detail > .exercise > h2{
    font-size: 2.4rem;
    margin-bottom: 20px;
    color: gray;
}

.detail > .exercise > ul > li{
    font-size: 1.5rem;
    color: tomato;
    margin-left: 30px;
}

.detail > .marqueeWork{
    margin: 0 25%;
    margin-top: 50px;
    font-size: 3.5rem;
    font-family: 'Lobster', cursive;
    box-shadow: 10px 10px 9px tomato;
    border-radius: 20px;
    color: gray;
    background-color: white;
}

.detail .video{
    margin: 100px 0;
    border-radius: 30px;
    padding: 0 25%;
}

.detail > .video > iframe {
    width: 100%;
    margin-top: 40px;
    border-radius: 25px;
}



@media only screen and (max-width: 768px){

    .detail-body{
        flex-direction: column;
        height: 670px;
    }

    .detail-body > .left {
        width: 95%;
        text-align: center;
        margin: 0 2.5%;
    }

    .detail-body > .left > p{
        width: 100%;
    }

    .detail-body > .left > ul{
        width: 100%;
        justify-content: space-evenly;
    }

    .detail-body > .left > h2{
        width: 100%;
    }

    .detail > .exercise{
        padding: 10% 0;
        padding-left: 5%;
    }

    .detail > .marqueeWork{
        font-size: 2rem !important;
        margin: 0 5%;
        margin-top: 40px;
    }

    .detail > .video {
        padding: 0;
        margin: 5% 0px;
    }
}

@media only screen and (max-width : 1000px){
    .detail > .marqueeWork {
        font-size: 3.5rem;
    }
}