.marquee {
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    box-sizing: border-box;
  }
  
  .marquee span {
    display: inline-block;
    width: 350%;
    padding-left: 75%;
    will-change: transform;
    animation: marquee 10s linear infinite;
    margin-bottom: 20px;
    color: #c29db9;
  }
  
  .marquee span:hover {
    animation-play-state: paused
  }
  
  
  @keyframes marquee {
    0% { transform: translate(0, 0); }
    100% { transform: translate(-100%, 0); }
  }
