.footer{
    text-align: center;
    width: 100%;
}

hr{
    margin: 110px auto;
    border-top: dotted;
    border-bottom: none;
    border-width: 6px;
    color: #0a1818;
    width: 5%;
}

.footer > a > button{
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 15px;
    border: none;
    background-color: #66BFBF;
    cursor: pointer;
    color: white;
    font-size: 1.3rem;
    font-family: 'Lobster', cursive;
}

.pagination {
    margin: 40px 0;
    font-family: fantasy;
}

.pagination > a{
    margin-right: 20px;
    text-decoration: none;
    color: #1C1E1E;
    padding: 10px;
    height: 10px;
}

.pagination > a:hover{
    background-color: #C9D4D4;
    border-radius: 100%;
}

.bottom-web{
    background-color: #66BFBF;
    padding: 40px 0;
    width: 100%;
    text-align: center;
   }
   
.bottom-web > .year{
       font-size: 1rem;
       color: gainsboro;
       margin-top: 30px;
   }

.bottom-web > a{
    margin:0 20px 0px 0;
}

.bottom-web > div > p{
    font-size: 1.3rem;
    color: whitesmoke;
}
