
.home{ 
    height: 100vh;
    width: 100%;
}

.home > img {
    width: 100%;
    height: 100%;
}

.home > .home-body {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    top: 0;
}

.home > .home-body > a{
    position: relative;
    top: 20px;
    text-align: center;
    width: 350px;
    height: 350px;
    box-shadow: 0 5px 20px white ;
    border-radius: 35px;
    text-decoration: none;
}

.home > .home-body > a:hover{
    transform: scale(1.02);
    transition: .8s;

}

.home > .home-body > a > .meals ,
.home > .home-body > a > .sport {
    text-align: center;
    box-shadow: 0 5px 20px white ;
    border-radius: 35px;
    background-color: #19002f;
}


.home > .home-body > a > .meals > img,
.home > .home-body > a > .sport > img{
    border-radius: 35px;
    width: 350px;
    height: 350px;
}

.home > .home-body > a > .meals > p,
.home > .home-body > a > .sport > p{
    font-size: 2rem;
    color: white;

}

.home > footer{
    width: 50%;
    position: absolute;
    bottom: 10px;
    text-align: center;
    left: 25%;
    color: teal;
}

.home > footer > .marqueeHome{
    font-size: 2rem;
}

@media only screen and (max-width: 768px){
    .home > .home-body{
        flex-direction: column;
    }

    .home > .home-body > a,
    .home > .home-body > a{
        box-shadow: none;
        width: 60%;
    }

    .home > .home-body > a > .meals ,
    .home > .home-body > a > .sport
    {
        height: 80%;
    }

    .home > .home-body > a:last-of-type{
        margin: 15% 0;
    }

    
    .home > .home-body > a > .meals > img,
    .home > .home-body > a > .sport > img
    {
        width: 100%;
        height: 87%;
    }
    .home > .home-body > a > .meals > p,
    .home > .home-body > a > .sport > p
    {
        font-size: 1.5rem;
    }

    
.home > footer > .marqueeHome{
    font-size: 1.3rem !important;
}

.home > footer{
    font-size: .9rem;
}

}

@media only screen and (max-height: 750px){
    .home > .home-body > a:last-of-type{
        margin: -15% 0;
    }
    .home > .home-body > a,
    .home > .home-body > a{
        box-shadow: none;
        width: 40;

    }

    .home > .home-body > a > .meals ,
    .home > .home-body > a > .sport
    {
        height: 60%;
    }

    
    .home > .home-body > a > .meals > img,
    .home > .home-body > a > .sport > img
    {
        width: 100%;
        height: 87%;
    }
    .home > .home-body > a > .meals > p,
    .home > .home-body > a > .sport > p
    {
        font-size: 1rem;
    }

    .home > footer{
        font-size: 0.7rem;
    }

}

@media only screen and (max-height: 650px){

    .home > .home-body > a:last-of-type{
        margin: -5% 0;
    }

    .home > .home-body > a:first-child{
        margin-bottom: 35px;
    }

    .home > .home-body > a{
        height: 200px;
        box-shadow: none;
        width: 50%;
    }

    .home > .home-body > a > .meals ,
    .home > .home-body > a > .sport
    {
        height: 100%;
    }

    
    .home > .home-body > a > .meals > img,
    .home > .home-body > a > .sport > img
    {
        width: 100%;
        height: 87%;
    }
    .home > .home-body > a > .meals > p,
    .home > .home-body > a > .sport > p
    {
        font-size: 1rem;
    }
}