.about{
    background-color: #EAF6F6;
    text-align: center;
    margin: 0px;
    font-family: 'Merriweather', serif;
    /* background-color: #696969; */
    background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
}

h1{
    color: #66BFBF;
    font-size: 5.625rem ;
    margin: 50px auto 0px auto;
    font-family: 'Sacramento', cursive;
}

h2{
    color: #66BFBF ;
    font-size: 3.5rem;
    font-family: 'Montserrat' , sans-serif;
    font-weight: normal;
}

.a-r{
    margin:0px 0px 0px 500px;
}

.a-l{
    margin:0px 500px 0px 0px; 
}

.middle> img{
    margin: 50px 0;
    width: 300px;
    border-radius: 100%;
    animation-name: wena;
    animation-iteration-count: infinite;
    animation-duration: 5s;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
}

.middle > img:hover{
    animation-name: wena1;
}

@keyframes wena{
    from{
        transform: scale(1);
    }
    to{
        transform: scale(1.10);
    }
}


@keyframes wena1{
    from{
         transform: none;
    }
}

.middle > p{
    font-size: 2rem;
    color: #11999E;
    font-family: cursive;
    margin: 0 20px;
}


hr{
    margin: 110px auto;
    border-top: dotted;
    border-bottom: none;
    border-width: 6px;
    color: #0a1818;
    width: 5%;
}


.codeSkill{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.codeSkill > .coding,
.codeSkill > .language{
    width: 300px;
    height: 100%;
    border-radius: 20px;
    box-shadow: 2px 2px 30px gray;
    padding: 60px 10px 40px 10px;
}

.codeSkill > .coding:hover,
.codeSkill > .language:hover{
    transform: scale(1.05);

}

.codeSkill > .coding{
    margin-top: 20px;
}

.codeSkill > .coding > img{
    border-radius: 20px;
}

.codeSkill > .coding > h3,
.codeSkill > .language > h3
{
    margin: 5px 0;
    font-family: cursive;
    color: gray;
}

.codeSkill > .coding > p,
.codeSkill > .language > p
{
    font-family: Arial, Helvetica, sans-serif;
    color: #3b8639;
}



@media only screen and (max-width: 700px) {

    .top > .a-r{
        margin-left: 60% ;
    }

    .top > .a-l{
        margin-right: 60%;
    }

    .top > h1{
        font-size: 4rem;
    }

    .top > h2{
        font-size: 2rem;
    }

    .middle > img{
        width: 50% !important;
    }

    .codeSkill {
        flex-direction: column;
        
    }
    
    .codeSkill > .coding{
        margin-bottom: 60px;
    }
}